* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.background {
  display: flex;
  width: 100%;
  height: 100vh;
  background-image: url(../images/space.jpg);
  background-size: cover;
  background-position: center;
}

.banner {
  position: fixed;
  bottom: -100%;
  width: 100%;
  left: 50%;
  margin-right: -50%;
  transform: translateX(-50%);
  max-width: 1024px;
  min-width: 280px;
  height: 90px;
  background-color: darkgreen;
  animation-name: banner;
  animation-fill-mode: forwards;
  animation-duration: 1.5s;

  .container {
    width: 100%;
    height: 100%;
    background-image: url("../images/girl-1024.png");
    background-repeat: no-repeat;
    background-size: cover;

    .content {
      height: 100%;
      display: flex;
      align-items: center;

      .logo {
        border: 2px solid #ffffff;
        display: flex;
        justify-content: center;
        padding: 10px 0;
        width: 156px;

        p {
          display: inline-block;
          margin: 0;
          color: white;
        }
      }

      .august {
        display: flex;
        align-items: center;
        height: 100%;

        .number {
          font-size: 86px;
          color: #96aca8;
          opacity: 0.8;
          font-weight: bold;
          line-height: 1;
        }

        .month {
          position: relative;
          right: 20px;
          transform: rotate(-90deg);
          font-weight: bold;
          background: #96aca8;
          opacity: 0.8;
          justify-self: flex-start;

          span {
            mix-blend-mode: overlay;
            margin: 0;
            padding: 2px;
            line-height: 1;
            font-weight: bold;
            font-size: 16px;
          }
        }
      }

      .text {
        font-size: 30px;
        color: white;
        font-weight: bold;
        span {
          font-size: 40px;
          color: white;
        }
        .for-you {
          color: #b5b5b5;
          font-weight: 100;
        }
      }
    }
  }

}


@keyframes banner {
  from {
    bottom: -100%;
  }
  to {
    bottom: 0;
  }
}
